.wrapper {
    /* width: 350px; */
    width: 850px;
    /* width: 400px; */
    height: 175px;
    margin-left: auto;
    margin-right: auto;
    border: 2px solid transparent;
    border-radius: 8px;
    background-image: -webkit-linear-gradient(white, white), -webkit-radial-gradient(top left, circle, #0714b6, #080665d1);
    background-image: linear-gradient(white, white), radial-gradient(circle at top left, #0714b6, #080665d1);
    background-origin: border-box;
    background-clip: content-box, border-box;
}

