body {
    margin: 0;
    font-size: 16px;
}

a {
    color: #00bcd4;
    font-weight: bold;
    text-decoration: none;
    transition: color 0.4s;
}

a:hover {
    color: #1976d2;
}

.text-center {
    text-align: center;
}

.top-bar {
    padding: 10px 15px;
    margin-bottom: 50px;
}

.top-bar::after {
    content: '';
    display: block;
    clear: both;
}

.top-bar-left {
    float: left;
    font-size: 1.5em;
}

.top-bar-right {
    float: right;
}

.top-bar a,
.nav a {
    margin: 0 8px;
}

.signup-container {
    margin: 0 auto;
    text-align: center;
    width: 350px;
}

h2 {
    padding-top: 25px;
}

.card-heading {
    padding: 25px;
}

.field-line, .button-line {
    padding: 10px;
}

.error-message {
    padding: 0 16px;
    color: tomato;
}

.success-message {
    padding: 0 16px;
    color: green;
}